import cuCustom from '@/colorui/components/cu-custom.vue'
import swiperTabHead from "@/components/tab/swiper-tab-head.vue"
export default {
	data() {
		return {
			isBottom: false,
			// tab切换
			tabBars: [{
					name: "全国",
					id: "quanguo"
				},
				{
					name: "同城",
					id: "tongcheng"
				}
			],
			swiperheight: 500, //高度
			tabIndex: 0, // 选中的
			pages: [],
			// 关注
			isClick: '',
			// 点赞
			isLike: '',
			// 点赞的人数
			LikeNum: 0
		}
	},
	onLoad() {
		uni.request({
			url: 'http://47.104.29.236:9999/api/v1/plaza',
			method: 'GET',
			data: {},
			success: res => {
				this.pages = res.data.data
				console.log(this.pages)
			},
			fail: () => {},
			complete: () => {}
		});
		uni.getSystemInfo({
			success: (res) => {
				let height = res.windowHeight - uni.upx2px(100)
				this.swiperheight = height;
			}
		});
	},
	components: {
		cuCustom,
		swiperTabHead
	},
	methods: {
		// 点赞
		likefill() {
			if (this.isLike == 0) {
				this.isLike = 1
				this.LikeNum = this.LikeNum + 1
				uni.showToast({
					title: '谢谢',
					duration: 2000
				});
			} else {
				this.isLike = 0
				this.LikeNum = this.LikeNum - 1
			}
		},
		// 关注
		like(index) {


		},
		// 跳转到广场详情页面
		openinfo(e) {
			// console.log(e)
			var plazaid = e
			uni.navigateTo({
				url: '../plaza_details/plaza_details?plazaid=' + plazaid
			});
		},
		// 跳转到广场个人
		openpersonal(user_id) {
			var userid = user_id
			// console.log(userid)
			uni.navigateTo({
				url: '../plaza_personal/plaza_personal?userid=' + userid
			});
		},
		// 跳转到发表页面
		openplaza_publish() {
			uni.navigateTo({
				url: '../plaza_publish/plaza_publish'
			});
		},
		//滑动切换导航
		tabChange(e) {
			this.tabIndex = e.detail.current;
		},
		//接受子组件传过来的值点击切换导航
		tabtap(index) {
			this.tabIndex = index;
		}
	}
}
